import { Component, OnInit } from '@angular/core';
import { Loader } from 'pixi.js';
import { Game } from '../../game';
import { ActivatedRoute } from '@angular/router';
import { DiscordOAuth2Service, DiscordInfo } from '../discord-oauth2.service';
import { userInfo } from 'os';

@Component({
  selector: 'app-game',
  templateUrl: './game.component.html',
  styleUrls: ['./game.component.scss']
})
export class GameComponent {

  private avatar: any;
  private userInfo: DiscordInfo;

  private loader = new Loader();
  private game: Game;

  constructor(private discordService: DiscordOAuth2Service, private route: ActivatedRoute) {

    console.log(this.route.queryParamMap.subscribe(querys => {
      if (querys.get('token')) {
        localStorage.setItem('discord-token', querys.get('token'));

        this.discordService.getUserAvatar().subscribe(avatar =>  this.avatar = avatar);
        this.discordService.getUserInfo().subscribe(userInfo => this.userInfo = userInfo);
      } else {
        // redirect back
      }
    }));

    this.game = new Game(window.innerWidth - 300, window.innerHeight, 0xffffff, false);
    this.game.setLoader(this.loader);
    this.game.start();

    window.onresize = (event: UIEvent) => {
      this.game.resize(window.innerWidth - 300, window.innerHeight);
    }
  }

}
